.Counter{
    //background-color: greenyellow;
    line-height: 20px;
}
.Counter .decrement{
    background: red;
    font-size: 30px;
    width: 35px;
    height: 35px;
    border-radius: 10%;
    border-width: 2px;


    -webkit-box-shadow: 0px 0px 4px 4px rgba(15,15,8,0.89);
    -moz-box-shadow: 0px 0px 4px 4px rgba(15,15,8,0.89);
    box-shadow: 0px 0px 4px 4px rgba(15,15,8,0.89);
    margin: 10px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    line-height: 1px;
    transition: background-color 0.2s linear;
}
.Counter .decrement:hover{
    background: gray;
}
.Counter .decrement:active{
    background: blue;
}

.Counter .increment{
    background: greenyellow;
    font-size: 30px;
    width: 35px;
    height: 35px;
    border-radius: 10%;
    border-width: 2px;


    -webkit-box-shadow: 0px 0px 4px 4px rgba(15,15,8,0.89);
    -moz-box-shadow: 0px 0px 4px 4px rgba(15,15,8,0.89);
    box-shadow: 0px 0px 4px 4px rgba(15,15,8,0.89);
    margin: 10px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    line-height: 1px;
}